/*级联选择器*/
.ms-organization-wrap {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  width: 300px;
}
.ms-organization-title {
  position: relative;
  width: 100%;
  height: 38px;
  line-height: 28px;
  padding: 5px 10px;
  border-bottom: 1px solid rgba(81, 130, 228, 0.5);
}
.ms-organization-icon {
  position: absolute;
  display: inline-block;
  top: 10px;
  right: 0px;
  font-size: 10px;
  color: #999;
}
.ms-organization-body {
  position: absolute;
  top: 38px;
  left: 0px;
  z-index: 100;
  width: 300px;
  height: auto;
  min-height: 100px;
  max-height: 300px;
  background: #fff;
  border-radius: 3px;
  overflow: hidden;
  box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1), 0 0 12px 5px rgba(0, 0, 0, 0.1);
}
.ms-organization-search {
  padding: 10px;
}
.ms-organization-search input {
  width: 100%;
  height: 34px;
  padding: 5px;
  border: none;
  border-bottom: 1px solid rgba(81, 130, 228, 0.5);
  outline: none;
}
.ms-organization-router {
  line-height: 20px;
  padding: 5px 10px;
  width: 100%;
  overflow-x: scroll;
}
.ms-organization-router a {
  cursor: pointer;
}
.ms-organization-body-top {
  bottom: 60px;
}
.ms-organization-content {
  /*padding: 15px 10px;*/
}
.ms-organization-options {
  list-style: none;
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 160px;
}
.ms-organization-options li {
  padding: 10px;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: rgba(10, 18, 32, 0.64);
}
.ms-organization-options li:hover {
  color: rgba(10, 18, 32, 0.87);
  background-color: rgba(232, 235, 237, 0.6);
}
.ms-organization-options li.active {
  color: #5182e4;
}



