
.ms-table-wrap {
  position: relative;
  width: 100%;
  overflow-y: hidden;
  overflow-x: scroll;
  background: #fff;
  border: 1px solid #e7e7eb;
}
.ms-table-wrap table {
  width: 100%;
  font-size: 14px;
  background-color: #fff;
  text-align: center;
  border: none;
  border-spacing: 0;
  border-collapse: collapse;
  box-sizing: border-box;
}
.ms-table-content {
  overflow-y: scroll;
  overflow-x: hidden;
}
.ms-table-content .ms-table_cell {
  border-top: 1px solid #e7e7eb;
  border-right: 1px solid #e7e7eb;
  text-align: center;
  padding: 10px 8px;
  min-width: 240px;
}
.ms-table-content .ms-table_thead .ms-table_cell {
  border-top: none;
}
.ms-table-sort {
  display: inline-block;
  position: relative;
  width: 10px;
  height: 12px;
  margin-left: 4px;
  margin-top: -1px;
  vertical-align: middle;
  overflow: hidden;
  cursor: pointer;
}
.ms-table-sort i {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  color: #bbbec4;
  transition: color 0.2s ease-in-out;
}
.ms-table-sort i.cur {
  border-color: transparent transparent #2d8cf0 transparent;
}
.ms-table-sort i:first-child {
  top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #bbbec4 transparent;
}
.ms-table-sort i:last-child {
  bottom: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #bbbec4 transparent transparent transparent;
}
.ms-table-sort i.cur:first-child {
  border-bottom-color: #2d8cf0;
}
.ms-table-sort i.cur:last-child {
  border-top-color: #2d8cf0;
}
.ms-table-tips {
  position: relative;
  cursor: pointer;
}
.ms-table-tips .ms-question-sign {
  display: inline-block;
  width: 16px;
  height: 16px;
  line-height: 16px;
  border-radius: 50%;
  color: #fff;
  font-size: 12px;
  background: #444;
  font-style: normal;
  overflow: hidden;
  vertical-align: middle;
}
.ms-table-tips .ms-question-sign_wrap {
  display: none;
  position: absolute;
  min-width: 200px;
  left: 50%;
  top: 20px;
  padding: 5px 10px;
  background: #000;
  border-radius: 3px;
  font-size: 12px;
  color: #fff;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  transition: all .5s;
}
.ms-table-tips:hover .ms-question-sign_wrap {
  display: block;
}
.fixed-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
  transition: all 0.3s;
  background: #fff;
}
.empty_data {
  padding: 100px 0;
  text-align: center;
  font-size: 14px;
  color: #8d8d8d;
}



